import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import Navigation from "../Navigation";

function Post12() {
  const location = useLocation();
  const themeModeInitial = location?.state?.themeMode
    ? location.state.themeMode
    : "light";
  const [themeMode, setThemeMode] = useState(themeModeInitial);
  const tags = ["turing-machine"];

  const toggleThemeMode = () => {
    if (themeMode === "light") {
      setThemeMode("dark");
    } else {
      setThemeMode("light");
    }
  };

  useEffect(() => {
    if (location?.state?.themeMode) {
      setThemeMode(location.state.themeMode);
    }
    window.scrollTo(0, 0);
  }, [location]);

  return (
    <div className={themeMode === "dark" ? "dark" : "light"}>
      <Navigation
        themeMode={themeMode}
        onThemeChange={toggleThemeMode}
      ></Navigation>
      <div className="flex flex-col items-center content min-h-[calc(100vh-100px)] dark:bg-slate-custom1">
        <div className="w-11/12 sm:w-11/12 md:w-9/12 lg:w-5/12 xl:w-4/12 mx-0">
          <div className="mt-16 mb-4 flex justify-items-start content">
            <p className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
              Catalog of Turing Machines
            </p>
            <div className="line-break"></div>
            <time className="text-gray-custom2 text-left text-sm lg:text-base w-full md:w-full dark:text-gray-custom1">
              Sep 8, 2024
            </time>
            <div className="line-break"></div>
            <div>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                Some years ago I read the book "The annotated Turing" by Charles
                Petzold, and I decided to implement in a web application some of
                the machines designed by Turing.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                The original paper from Turing created the basis for the theory
                of computation, and introduced the concept of Turing Machine.
                Turing created a machine that, starting from basic operations,
                was able to produce computable numbers.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                Turing started to describe the concept of m-configurations as
                the finite number of configurations (states) in a machine.
                Turing also added a physical structure to the machine with the
                concept of "tape", a one-dimensional tape divided into squares.
                These squares can contain a symbol, and the machine can read
                from and write to the squares of the tape.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                A limitation imposed by Turing is that the machine can only scan
                one square at a time. The machine switches from one
                m-configuration to another depending on the scanned symbol. The
                m-configurations can be used to retain information about the
                symbols that have been scanned previously.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                At any moment in time the configuration of the Turing Machine is
                determined by the m-configuration and the scanned symbol. The
                operations available on the machine are: print (P), erase (E),
                move right (R), move left (L). The machine prints the symbols 0
                and 1, and these symbols constitute the computed sequence in the
                tape. It's important to mention that Turing in his paper
                describes machines that require the tape to extend just on the
                right direction.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                The following is the first machine defined by Turing in the
                paper:
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                <table>
                  <tr>
                    <th>m-config</th>
                    <th>symbol</th>
                    <th>operations</th>
                    <th>final m-config</th>
                  </tr>
                  <tr>
                    <td>b</td>
                    <td>None</td>
                    <td>P0,R</td>
                    <td>c</td>
                  </tr>
                  <tr>
                    <td>c</td>
                    <td>None</td>
                    <td>R</td>
                    <td>e</td>
                  </tr>
                  <tr>
                    <td>e</td>
                    <td>None</td>
                    <td>P1,R</td>
                    <td>f</td>
                  </tr>
                  <tr>
                    <td>f</td>
                    <td>None</td>
                    <td>R</td>
                    <td>b</td>
                  </tr>
                </table>
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                Compared to the original paper, I used the latin letters instead
                of the German letters for the m-configurations.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                In order for Turing to create advanced machines, he introduced
                the symbols schwa, and x. The symbol schwa is used as a
                sentinel, and in the Turing machines two schwa symbols at the
                beginning of the tape indicate the left boundary of the number.
                The symbols 0 and 1 are always printed with a space of one
                square in between. The intermediate square is used to print the
                symbol x when required by the machine, and its used as a sort of
                temporary memory.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                I have created a web application that implements some of the
                machines described by Turing in his paper. The application
                retains the concept of tape, m-configuration, symbol, and
                operations. I have used latin letters in the machine
                configuration and the tape, and I replaced the symbol schwa with
                the letter "s".
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                Starting from the current m-configuration and the scanned
                symbol, at each stage the application executes the operations
                listed in the current m-configuration and moves to the final
                m-configuration. I made the design choice to print the complete
                configuration of the machine in a new line for each stage of
                execution, so the complete flow can be tracked from start to
                finish. Another design choice I made was to limit the total
                number of squares in the tape, defined by the user in the
                initial configuration, so the tape does not extend indefinitely.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                The GitHub repository of the application is the following:
                <br />
                <a
                  className="underline"
                  href="https://github.com/sitMCella/turing-machine"
                  target="_blank"
                  rel="noreferrer"
                >
                  sitMCella/turing-machine
                </a>
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                A running version of the web application is the following:
                <br />
                <a
                  className="underline"
                  href="https://sitmcella.github.io/turing-machine/"
                  target="_blank"
                  rel="noreferrer"
                >
                  sitMCella/turing-machine/
                </a>
              </p>
            </div>
          </div>
          <div className="mt-4 mb-16 text-left">
            {tags.map((item, index) => (
              <span
                key={`tag-${index}`}
                className="leading-7 mt-4 bg-blue-100 text-blue-800 dark:bg-gray-600 dark:text-gray-custom1 text-xs lg:text-sm font-medium me-2 px-2.5 py-0.5 rounded-full dark:bg-blue-900 dark:text-blue-300 text-left text-base prose w-11/12 md:w-full"
              >
                {item}
              </span>
            ))}
          </div>
        </div>
        <div></div>
      </div>
    </div>
  );
}

export default Post12;
