import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import Navigation from "../Navigation";

function Post4() {
  const location = useLocation();
  const themeModeInitial = location?.state?.themeMode
    ? location.state.themeMode
    : "light";
  const [themeMode, setThemeMode] = useState(themeModeInitial);
  const tags = ["blog", "react", "css"];

  const toggleThemeMode = () => {
    if (themeMode === "light") {
      setThemeMode("dark");
    } else {
      setThemeMode("light");
    }
  };

  useEffect(() => {
    if (location?.state?.themeMode) {
      setThemeMode(location.state.themeMode);
    }
    window.scrollTo(0, 0);
  }, [location]);

  return (
    <div className={themeMode === "dark" ? "dark" : "light"}>
      <Navigation
        themeMode={themeMode}
        onThemeChange={toggleThemeMode}
      ></Navigation>
      <div className="flex flex-col items-center content min-h-[calc(100vh-100px)] dark:bg-slate-custom1">
        <div className="w-11/12 sm:w-11/12 md:w-9/12 lg:w-5/12 xl:w-4/12 mx-0">
          <div className="mt-16 mb-4 flex justify-items-start content">
            <p className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
              Blog Template
            </p>
            <div className="line-break"></div>
            <time className="text-gray-custom2 text-left text-sm lg:text-base w-full md:w-full dark:text-gray-custom1">
              Aug 24, 2024
            </time>
            <div className="line-break"></div>
            <div>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                I released the template of the web application I developed for
                this web site.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                You can find the code in the following GitHub repository:
                <br />
                <a
                  className="underline"
                  href="https://github.com/sitMCella/blog-template"
                  target="_blank"
                  rel="noreferrer"
                >
                  sitMCella/blog-template
                </a>
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                The web application uses a custom blog design and is built using
                React and Tailwind CSS. In addition, the web application uses
                Prism.js to display the code snippets. The React web application
                makes use of the Single Page Application model.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                The React application has been adjusted for the single page
                application to be supported in GitHub Pages [1].
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                A design choice for this web application is the use a
                minimalistic style. The navigation bar on the top of the page
                contains concise menu options. The Blog page lists all of the
                posts using simple cards, with the headline of the article, the
                date, the excerpt, and the Tags. In addition, the Blog page
                contains a search bar to filter the articles based on one Tag.
                You can start typing the word on the text box, and then select
                one Tag from the dropdown.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                The pages that contain the articles keep a minimalist aesthetic,
                and these make use of panels and code snippets. Links and
                references can be either appear in the text of the article, or
                on the bottom of the page.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                Probably in future I will add new features (for example the dark
                mode), and I will apply changes on some components of the pages.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                [1]{" "}
                <a
                  className="underline"
                  href="https://github.com/rafgraph/spa-github-pages"
                  target="_blank"
                  rel="noreferrer"
                >
                  spa-github-pages
                </a>
              </p>
            </div>
          </div>
          <div className="mt-4 mb-16 text-left">
            {tags.map((item, index) => (
              <span
                key={`tag-${index}`}
                className="leading-7 mt-4 bg-blue-100 text-blue-800 dark:bg-gray-600 dark:text-gray-custom1 text-xs lg:text-sm font-medium me-2 px-2.5 py-0.5 rounded-full dark:bg-blue-900 dark:text-blue-300 text-left text-base prose w-11/12 md:w-full"
              >
                {item}
              </span>
            ))}
          </div>
        </div>
        <div></div>
      </div>
    </div>
  );
}

export default Post4;
