import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import Navigation from "../Navigation";

function Post15() {
  const location = useLocation();
  const themeModeInitial = location?.state?.themeMode
    ? location.state.themeMode
    : "light";
  const [themeMode, setThemeMode] = useState(themeModeInitial);
  const tags = ["azure", "function", "ssl"];

  const toggleThemeMode = () => {
    if (themeMode === "light") {
      setThemeMode("dark");
    } else {
      setThemeMode("light");
    }
  };

  useEffect(() => {
    if (location?.state?.themeMode) {
      setThemeMode(location.state.themeMode);
    }
    window.scrollTo(0, 0);
  }, [location]);

  return (
    <div className={themeMode === "dark" ? "dark" : "light"}>
      <Navigation
        themeMode={themeMode}
        onThemeChange={toggleThemeMode}
      ></Navigation>
      <div className="flex flex-col items-center content min-h-[calc(100vh-100px)] dark:bg-slate-custom1">
        <div className="w-11/12 sm:w-11/12 md:w-9/12 lg:w-5/12 xl:w-4/12 mx-0">
          <div className="mt-16 mb-4 flex justify-items-start content">
            <p className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
              SSL certificate renewal in Azure
            </p>
            <div className="line-break"></div>
            <time className="text-gray-custom2 text-left text-sm lg:text-base w-full md:w-full dark:text-gray-custom1">
              Oct 11, 2024
            </time>
            <div className="line-break"></div>
            <div>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                Azure Application Gateways support SSL termination by allowing
                SSL certificates to be added to the listeners in the Application
                Gateway configuration.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                In this post I will describe a solution for the renewal of SSL
                certificates issued by Let's Encrypt. The solution makes use of
                an automation procedure using PowerShell and Azure Function.
                Terraform is used for provisioning the solution in Azure.
                <br />
                The GitHub project is the following:
                <br />
                <a
                  className="underline"
                  href="https://github.com/sitMCella/azure-terraform-ssl-certificate-renewal"
                  target="_blank"
                  rel="noreferrer"
                >
                  sitMCella/azure-terraform-ssl-certificate-renewal
                </a>
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                The automation procedure generates new SSL Certificates using
                Let's Encrypt and stores the SSL certificates in a certificate
                object in Azure Key Vault. The SSL certificate is then used by
                one Application Gateway listener associated to a web application
                hosted in one Azure App Service.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                The SSL Certificates must include a domain name. You can
                purchase a custom domain from a domain name registrar such as
                Namecheap. The SSL certificates issued by Let's Encrypt are
                valid for 90 days, and Let's Encrypt recommends to renew the SSL
                certificates every 60 days. The procedure for generating a new
                SSL certificate requires the use of an ACME client. The client
                module ACME-PS is available in PowerShell.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                The automation procedure executes the following steps: creates a
                new ACME order, stores an ACME challenge in a Storage Account,
                generates a new SSL certificate, and imports the PFX file of the
                SSL certificate in one Azure Key Vault. The PowerShell script
                with the automation procedure can be found in:
                <br />
                <a
                  className="underline"
                  href="https://github.com/sitMCella/azure-terraform-ssl-certificate-renewal/blob/main/modules/ssl_certificate_renewal/function/TimerTrigger1/run.ps1"
                  target="_blank"
                  rel="noreferrer"
                >
                  function/TimerTrigger1/run.ps1
                </a>
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                In order to issue one SSL certificate, a new ACME order must be
                created and a challenge must be generated and stored as a file
                to ensure the ownership of the domain name that is going to be
                included in the SSL certificate.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                In addition, the ACME challenge file must be reachable from the
                Internet using the custom domain name. I decided to store the
                ACME challenge in one Azure Storage Account configured as a
                Static Website. One Azure DNS Zone is provisioned with the name
                of the custom domain, and a CNAME record is configured with a
                subdomain of the custom domain. In the example project the
                subdomain is called "app". In order to host your custom domain
                in Azure DNS, you have to configure the name servers (NS) of the
                Azure DNS Zone in the domain name registrar as NS records.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                Initially the CNAME record in the Azure DNS Zone maps the
                subdomain of the custom domain to the FQDN name of the static
                website. This temporary DNS configuration is used while
                generating the first SSL certificate. The ACME challenge must be
                stored as a blob in the Storage Account inside a folder with
                name ".well-known/acme-challenge" in the container "$web".
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                When the PowerShell script in the Azure Function generates the
                first SSL certificate and stores the certificate in the Key
                Vault, then the Azure Application Gateway can be configured as
                follows: one listener is configured with the SSL Certificate
                stored in the Key Vault, and is associated to the web
                application backend; a second listener is configured with the
                HTTP protocol, and is associated to the Static Website backend.
                The CNAME record in the Azure DNS Zone is then configured with
                the Public IP Address of the Azure Application Gateway.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                This ensures that Let's Encrypt will be able to access the
                challenge stored in the Static Website for every subsequent
                executions of the PowerShell script hosted in the Azure
                Function.
              </p>
            </div>
          </div>
          <div className="mt-4 mb-16 text-left">
            {tags.map((item, index) => (
              <span
                key={`tag-${index}`}
                className="leading-7 mt-4 bg-blue-100 text-blue-800 dark:bg-gray-600 dark:text-gray-custom1 text-xs lg:text-sm font-medium me-2 px-2.5 py-0.5 rounded-full dark:bg-blue-900 dark:text-blue-300 text-left text-base prose w-11/12 md:w-full"
              >
                {item}
              </span>
            ))}
          </div>
        </div>
        <div></div>
      </div>
    </div>
  );
}

export default Post15;
