import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import Navigation from "../Navigation";

function Post8() {
  const location = useLocation();
  const themeModeInitial = location?.state?.themeMode
    ? location.state.themeMode
    : "light";
  const [themeMode, setThemeMode] = useState(themeModeInitial);
  const tags = ["story-outline"];

  const toggleThemeMode = () => {
    if (themeMode === "light") {
      setThemeMode("dark");
    } else {
      setThemeMode("light");
    }
  };

  useEffect(() => {
    if (location?.state?.themeMode) {
      setThemeMode(location.state.themeMode);
    }
    window.scrollTo(0, 0);
  }, [location]);

  return (
    <div className={themeMode === "dark" ? "dark" : "light"}>
      <Navigation
        themeMode={themeMode}
        onThemeChange={toggleThemeMode}
      ></Navigation>
      <div className="flex flex-col items-center content min-h-[calc(100vh-100px)] dark:bg-slate-custom1">
        <div className="w-11/12 sm:w-11/12 md:w-9/12 lg:w-5/12 xl:w-4/12 mx-0">
          <div className="mt-16 mb-4 flex justify-items-start content">
            <p className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
              Story Outlining Web Application
            </p>
            <div className="line-break"></div>
            <time className="text-gray-custom2 text-left text-sm lg:text-base w-full md:w-full dark:text-gray-custom1">
              Sep 2, 2024
            </time>
            <div className="line-break"></div>
            <div>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                I developed a story outlining web application. A story outline
                defines the roadmap for a story, and provides a structure for
                writing a manuscript, guiding the writer from the beginning to
                the end.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                Some of the components of a story outline are the characters
                definition, the plots, and the scenes. A plot consists of the
                sequence of events that constitute the story. A scene instead is
                composed of a series of continuous (self-contained) events, and
                is part of a plot. The scenes of a story can also be grouped in
                different chapters of the manuscript.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                The web application represents a book as a Project. The project
                consists of a title, a description, and a list of Tags. The Tags
                are configured at Project level, in order to maintan consistency
                over the Tags assigned to the scenes of the story. You can
                decide for example to create one Tag for every character, and
                later on track on which scenes each character appears throughout
                the story.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                I decided to provide two different structures for the story
                outline in the Project Map page and the Project Book page.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                The Project Map page defines a storyboard that collects the
                scenes in a tabular format with the chapters as columns and the
                plots as rows. The scenes are cards that shows the extract of
                the scene, and can be expanded to get access to the content and
                the configuration of the scene. The Project Map page allows you
                to plan and structure the scenes in plots and chapters. In
                addition you can assign Tags to the scenes and filter the scenes
                by Tags.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                The Project Book page instead shows the list of scenes. In this
                page the scene editor is always visible, but you can't configure
                the scenes or filter the scenes by Tag.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                The GitHub repository of the story outlining web application is
                the following:
                <br />
                <a
                  className="underline"
                  href="https://github.com/sitMCella/book-advisor"
                  target="_blank"
                  rel="noreferrer"
                >
                  sitMCella/book-advisor
                </a>
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
                In this article I described the scope and the concepts of the
                story outlining web application. I have already covered in
                previous posts the technical details of the web application.
              </p>
            </div>
          </div>
          <div className="mt-4 mb-16 text-left">
            {tags.map((item, index) => (
              <span
                key={`tag-${index}`}
                className="leading-7 mt-4 bg-blue-100 text-blue-800 dark:bg-gray-600 dark:text-gray-custom1 text-xs lg:text-sm font-medium me-2 px-2.5 py-0.5 rounded-full dark:bg-blue-900 dark:text-blue-300 text-left text-base prose w-11/12 md:w-full"
              >
                {item}
              </span>
            ))}
          </div>
        </div>
        <div></div>
      </div>
    </div>
  );
}

export default Post8;
