import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import Navigation from "./Navigation";
import "./Profile.css";

function Profile() {
  const location = useLocation();
  const themeModeInitial = location.state
    ? location?.state?.themeMode
    : "light";
  const [themeMode, setThemeMode] = useState(themeModeInitial);

  const toggleThemeMode = () => {
    if (themeMode === "light") {
      setThemeMode("dark");
    } else {
      setThemeMode("light");
    }
  };

  useEffect(() => {
    if (location?.state?.themeMode) {
      setThemeMode(location.state.themeMode);
    }
  }, [location]);

  return (
    <div className={themeMode === "dark" ? "dark" : "light"}>
      <Navigation
        page="profile"
        themeMode={themeMode}
        onThemeChange={toggleThemeMode}
      ></Navigation>
      <div className="flex flex-col items-center content min-h-[calc(100vh-100px)] dark:bg-slate-custom1">
        <div className="w-11/12 sm:w-11/12 md:w-9/12 lg:w-5/12 xl:w-4/12 mx-0">
          <div className="mt-16 flex justify-items-start content">
            <p className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
              About me
            </p>
            <div className="line-break"></div>
            <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
              My name is Marco Cella, I am based in Koeln, Germany.
              <br />I am passionate about software development and cloud, and I
              have 15 years of experience in the IT industry.
            </p>
            <div className="line-break"></div>
            <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-11/12 md:w-full">
              I decided to create this blog to share my experience and hopefully
              some useful tips on different technologies and frameworks.
            </p>
          </div>
          <div className="mt-16 flex justify-items-start content">
            <p className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
              Elsewhere
            </p>
            <div className="line-break"></div>
            <p className="mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert">
              <a
                className="underline"
                href="https://www.linkedin.com/in/marco-cella-48391a5b"
              >
                Linkedin
              </a>
            </p>
            <div className="line-break"></div>
            <p className="mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert">
              <a className="underline" href="https://github.com/sitMCella">
                GitHub
              </a>
            </p>
          </div>
        </div>
        <div></div>
      </div>
    </div>
  );
}

export default Profile;
